@media screen and ( max-width:1200px ) {
  .menu {
    &_item {
      padding-right: 20px; }
    &_link {
      font-size: 13px; } }
  .subheader {
    &_call {
      text-align: center; } }
  .promo {
    &_header {
      font-size: 50px; } }
  .reasons {
    &_block {
      align-items: flex-start; }
    &_descr {
      width: 300px; } }
  .footer {
    .links_app {
      display: flex;
      margin-left: 900px;
      flex-wrap: wrap;
      top: 30px;
      &_to_icon {

        margin-bottom: 10px;
        margin-left: 5px; } } } }


@media (max-width:991px) {
  .menu {
    &_item {
      padding-right: 8px; }
    &_link {
      font-size: 10px; } }
  .subheader {
    &_logo {
      img {
        width: 100%; } }
    &_official {
      font-size: 11px; }
    &_phone {
      font-size: 18px; }
    &_btn {
      margin-top: 20px;
      width: 100%;
      height: 40px; } }
  .promo {
    &_header {
      font-size: 40px; }
    &_subheader {
      font-size: 30px; } }
  .reasons {
    &_block {
      flex-direction: column;
      align-items: center; }
    &_round {
      margin-right: 0; }
    &_descr {
      margin-top: 20px;
      width: 300px; } }
  .auto {
    min-height: 393px;
    padding: 26px 0 31px 0;
    &_img {
      width: 100%; } }
  .footer {
    .links_app {

      top: 20px;
      width: 100px;
      display: flex;
      flex-wrap: wrap;
      &_to_icon {
        margin-bottom: 10px; } }
    .social {
      top: 20px; } } }






@media (max-width:767px) {
  .menu {
    position: fixed;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 0;
    left: -100%;
    width: 50%;
    height: 100vh;
    padding: 20px 15px;
    background-color: rgba(0, 0, 0, .8);
    transition: 0.6s all;
    &_active {
      left: 0; }
    &_item {
      padding-right: 0;
      border: none;
      margin: 10px 0; }
    &_link {
      font-size: 20px; } }
  .hamburger {
    display: block;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 22px;
    height: 12px;
    span {
      display: block;
      height: 2px;
      width: 100%;
      background-color: white;
      margin-bottom: 2px;
      transition: .5s all;
      cursor: pointer; }
    &_active {
      span {
        margin-bottom: -2px;
        &:nth-child(1) {
          transform: translateY(3px) rotate(-45deg); }
        &:nth-child(2) {
          display: none; }
        &:nth-child(3) {
          transform: translateY(3px) rotate(45deg); } } } }
  .xs_hidden {
    display: none; }
  .xs-visible {
    display: block; }
  .subheader {
    &_phone {
      font-weight: 400;
      font-size: 16px;
      text-align: center; }
    &_btn {
      display: block;
      width: 200px;
      height: 40px;
      margin: 0 auto;
      margin-top: 10px; } }

  .promo {
    &_header {
      line-height: 45px; } }
  .footer {

    .social {
      width: 82px;
      display: flex;
      flex-wrap: wrap;
      justify-content: start;
      &_item {
        padding: 3px; } } } }










@media (max-width:575px) {
