      font-size: 12px; }
    &_btn {
      margin-top: 7px;
      width: 110px;
      height: 22px;
      font-weight: 400;
      font-size: 9px; } }
  .promo {
    padding: 119px 0 35px 0;
    min-height: 400px;
    &_descr {
