      font-size: 20px;
      line-height: 30px;
      padding: 0 10px; }
    &_btn {
      margin-top: 72px;
      width: 210px;
      height: 50px;
      font-size: 16px; } }

  .menu {
    width: 85%;
    &_item {
      width: 100%;
      text-align: center;
