  .title {
    font-size: 16px;
    line-height: 16px;
    margin-top: 15px; }


  .mobile {
    padding: 27px 0;
    align-content: center;
    &_card {
      width: 290px;
      height: 254px;

      &_text {
        font-size: 18px;
