        margin: 0 3px 0 3px; } }


    .links_app {
      max-width: 80%;
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;
      margin:  0 auto;
      position: static;
      flex-direction: row;
      width: 285px;
      &_to_icon {
