    .social {
      display: flex;
      position: static;
      margin:  0 auto;
      margin-bottom: 19px;
      margin-top: 16px;
      flex-wrap: nowrap;
      width: 164px; } }


  .benefits {
    font-size: 11px;
    height: 22px;
    min-width: 75px; }
  .title {
    font-size: 16px; }
  .subtitle {
    font-size: 14px; } }























